/* table, th, td { border: 1px solid } */
table { border-spacing: 0px; color: #53555f; width: 100%; }

tbody tr:nth-child(odd) { background-color: #f5f5fa }
tbody tr.row-checked { background-color: #F0F6FF }
tbody tr { transition: 0.2s; height: 52px; }
tbody tr:hover { border-radius: 4px; color: #33333f; box-shadow: 0 0 8px #f5f5fa; }
tbody tr:hover .photo { opacity: 1; }

thead th, tbody td { padding: 0 16px }
thead tr { height: 44px }
tbody td:last-child { width: 100px; }

.text-left { text-align: left }
.text-right { text-align: right }
.text-center { text-align: center }

.pointer { cursor: pointer }
.bold { font-weight: 500; }
.mr-10 { margin-left: 10px; }
.ml-16 { margin-left: 16px; }

.icon { opacity: 0; transition: 0.2s; }
.photo { border-radius: 100px; width: 32px; height: 32px; opacity: 0.7; transition: 0.2s; position: absolute; }
.checkbox-column { height: 52px; padding-left: 32px; display: flex; align-items: center; justify-content: center; cursor: pointer; }
.checkbox-column:hover .icon,
.checkbox-column.checked .icon { opacity: 1; }
.checkbox-column:hover .photo,
.checkbox-column.checked .photo { opacity: 0; }

.btn-secondary { font-family: inherit; text-transform: uppercase; font-weight: 500; color: #53555f; background-color: #ffffff; border: 1px solid #e1e3ea; border-radius: 4px; padding: 8px 16px; cursor: pointer; transition: 0.2s; }
.btn-secondary:hover { border-color: transparent; box-shadow: 0 0 4px #d1d3dd; color: #2979ff; }
.btn-secondary:disabled { box-shadow: none; background-color: #e5e5ea; color: #80828a; cursor: not-allowed; }

.text-primary { color: #75a7ff; }
.text-warning { color: #ffc163; }
.text-danger { color: #ff7272; }
.text-gray { color: #53555f; }

.above-table { text-align: right; padding: 16px 16px 0; height: 40px; }

tbody tr.empty-state:nth-child(odd) { background-color: #ffffff; filter: drop-shadow(0 0 1px #d1d3dd); }
.empty-state td div { background-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #EDEEF3 52.08%, rgba(0, 0, 0, 0) 100%); height: 20px; color: transparent; margin: 15px 5%; animation-name: animateEmptyState; animation-duration: 1.5s; animation-timing-function: ease; animation-delay: 0s; animation-iteration-count: infinite; animation-direction: normal; animation-fill-mode: forwards; }
.empty-state img { background-color: #F5F5FA; width: 28px; height: 28px; border-radius: 100% }
.empty-state .btn-group.flatdropdown > a svg { fill: #D1D3DD; }

@keyframes animateEmptyState {
	0% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 52%, transparent 100%); } 3% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 55%, transparent 100%); } 6% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 58%, transparent 100%); } 9% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 61%, transparent 100%); } 12% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 64%, transparent 100%); } 15% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 67%, transparent 100%); } 18% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 70%, transparent 100%); } 21% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 73%, transparent 100%); } 24% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 76%, transparent 100%); } 27% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 75%, transparent 100%); } 30% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 72%, transparent 100%); } 33% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 69%, transparent 100%); } 36% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 66%, transparent 100%); } 39% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 63%, transparent 100%); } 42% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 60%, transparent 100%); } 45% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 57%, transparent 100%); } 48% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 54%, transparent 100%); } 51% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 51%, transparent 100%); } 54% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 48%, transparent 100%); } 57% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 45%, transparent 100%); } 60% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 42%, transparent 100%); } 63% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 39%, transparent 100%); } 66% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 36%, transparent 100%); } 69% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 33%, transparent 100%); } 72% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 30%, transparent 100%); } 75% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 27%, transparent 100%); } 78% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 30%, transparent 100%); } 81% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 33%, transparent 100%); } 84% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 36%, transparent 100%); } 87% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 39%, transparent 100%); } 90% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 42%, transparent 100%); } 93% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 45%, transparent 100%); } 96% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 48%, transparent 100%); } 100% { background-image: linear-gradient(90deg, transparent 0%, #EDEEF3 52%, transparent 100%); }
}

