.flex { display: flex }
.align-center { align-items: center }
.justify-space-between { justify-content: space-between; }

.padding-16 { padding: 16px; }
.margin-left-16 { margin-left: 16px; }


.labels {  margin: 16px 16px 0 0; padding: 16px; border: 1px solid #e1e3ea; border-radius: 4px; display: flex; flex-direction: column; height: 76px; justify-content: space-between; max-width: fit-content; }
.labels span { font-size: 14px; font-weight: 600; margin-right: 12px; }
.labels div { font-size: 12px; }

